import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { UserAuthService } from 'src/app/service/user-auth.service';
import { AuthenticationService } from 'src/app/service/authentication.service';
import { FormGroup, FormBuilder, Validators, NgForm } from '@angular/forms';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})

export class LoginComponent implements OnInit {

  isLoginValid = true;
  authSource: string;
  isMenuItemIdPresent = false;
  error: string;
  user: string;
  pwd: string;
  status:boolean;
  loginForm: FormGroup = this.fb.group({
    username: ['', [Validators.required,Validators.minLength(4),
      Validators.maxLength(20),Validators.pattern('^[a-zA-Z0-9]*')]],
    password: ['',Validators.required]
  });
  constructor(
    private userAuthService: UserAuthService,
    private authenticationService: AuthenticationService,
    private router: Router, private fb: FormBuilder,
    private route: ActivatedRoute) { }
  ngOnInit() {

    // if (this.userAuthService.menuItemId != null) {
    //   this.isMenuItemIdPresent = true;
    // }
    this.route.queryParams.subscribe((params: Params) => {
      this.authSource = params['from'];
    });
  }
  onSubmit(loginForm: any) {
    // this.user = this.loginForm.value.username;
    // this.pwd = this.loginForm.value.password;
    console.log(loginForm.value.username);
    console.log(loginForm.value.password);
    this.authenticationService.authenticate(loginForm.value.username, loginForm.value.password).subscribe((data) => {
      console.log(this.loginForm.value.username);
      console.log(this.loginForm.value.password);
      // this.authService.logIn(form.value.username, form.value.password);
      // console.log(data);
      this.userAuthService.loggedIn = true;
      this.status = this.userAuthService.loggedIn;
      console.log("role: " + data.role);
      console.log("user: " + data.token);
      console.log("user: " + data.user);
      console.log("userId: "+ data.id);
      console.log("Logged in...");
      this.userAuthService.setToken(data.token);
      this.userAuthService.setRole(data.role);
      this.userAuthService.setUser(data.user);
      this.userAuthService.setId(data.id);
      this.router.navigate(['/vehicle']);
    },
      (error) => {
        this.isLoginValid = false;
        // this.router.navigate([this.userAuthService.redirectUrl]);
        console.log(`hey error ` + JSON.stringify(error));
        if (error.status == 401) {
          this.error = "Invalid username/password";
        }
       
      });

    // const username = form.value.username;
    // const password = form.value.password;
    // if (username === 'john') { // temporary to show the invalid user login
    //   this.isLoginValid = false;
    // } else {
    //   this.authService.logIn(username, password);
    //   this.router.navigate([this.authService.redirectUrl]);
    // }
  }

  get username() {
    return this.loginForm.get('username');
  }
  get password() {
    return this.loginForm.get('password');
  }
}
